<template>
  <div class="vm">
    <h2 class="h-title"></h2>
    <div ref="popup" class="popup" v-show="shopPopup"></div>
    <div id="map" class="map-x"></div>
    <div class="explain">
      <p>Overlaydemo</p>
      <p>HTML</p>
      <hr>
      <p>assetsimportrequire</p>
    </div>
  </div>
</template>

<script>
import 'ol/ol.css'
import { Map, View, Feature } from 'ol'
import Tile from 'ol/layer/Tile'
import TileJSON from 'ol/source/TileJSON'
import Point from 'ol/geom/Point'
import * as style from 'ol/style'
import sourceVector from 'ol/source/Vector'
import layerVecor from 'ol/layer/Vector'
import Overlay from 'ol/Overlay'

// 
import iconPng from '@/assets/images/icon.png'

export default {
  name: 'SimplenessLabel',
  data () {
    return {
      map: null,
      overlay: null,
      popup: null,
      shopPopup: false
    }
  },
  methods: {
    initMap () {
      // 
      let iconFeature = new Feature({
        geometry: new Point([0, 0]), // 
        name: '',
        population: 4000,
        rainfall: 500
      })

      // 
      let iconStyle = new style.Style({
        image: new style.Icon({ // 
          anchor: [0.5, 46], //  [0, 0] 
          anchorXUnits: 'fraction',
          anchorYUnits: 'pixels',
          // 
          // src: require('../../assets/images/icon.png')
          // 
          src: iconPng
        })
      })

      // 
      iconFeature.setStyle(iconStyle)

      // 
      let vectorSource = new sourceVector({
        features: [iconFeature]
      })

      // 
      let vectorLayer = new layerVecor({
        source: vectorSource
      })

      // 
      let rasterLayer = new Tile({
        source: new TileJSON({
          url: 'https://api.tiles.mapbox.com/v4/mapbox.geography-class.json?secure&access_token=pk.eyJ1IjoiYWhvY2V2YXIiLCJhIjoiY2pzbmg0Nmk5MGF5NzQzbzRnbDNoeHJrbiJ9.7_-_gL8ur7ZtEiNwRfCy7Q',
          crossOrigin: ''
        })
      })

      this.map = new Map({
        target: 'map',
        layers: [rasterLayer, vectorLayer],
        view: new View({
          center: [0, 0],
          zoom: 3
        })
      })

      // 
      this.map.on('singleclick', e => {
        let elPopup = this.$refs.popup
        this.popup = new Overlay({
          element: elPopup,
          positioning: 'bottom-center',
          stopEvent: false,
          offset: [0, -50]
        })
        this.map.addOverlay(this.popup)
        let feature = this.map.forEachFeatureAtPixel(e.pixel, feature => feature)
        
        if (feature) {
          this.shopPopup = true
          elPopup.innerHTML = feature.values_.name
          let coordinates = feature.getGeometry().getCoordinates()
          setTimeout(() => {
            this.popup.setPosition(coordinates)
          }, 0)
        } else {
          this.shopPopup = false
        }
      })
    }
  },
  mounted () {
    this.initMap()
  }
}
</script>

<style lang="scss" scoped>
  .popup {
    background: #fff;
    padding: 10px 16px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, .5);
  }
</style>